.FeedItem_size_m {
  grid-column: auto / span 3;
  grid-template-areas: "header header" "content content" "footer controls";
  grid-template-rows: -webkit-min-content minmax(10rem, auto) -webkit-min-content;
  grid-template-rows: min-content minmax(10rem, auto) min-content;
  grid-template-columns: auto 40px; }
  .FeedItem_size_m .FeedItem-Description {
    -webkit-line-clamp: 3;
    max-height: 4.287em; }
  .FeedItem_size_m .FeedItemFeedback {
    padding-top: 0;
    padding-bottom: 0;
    margin-right: 0;
    grid-row: unset; }



